/* counter cards */
.projectFactsWrap {
  display: flex;
  margin-top: 30px;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
/* .projectFactsWrap .item {
  width: 23%;
  margin-bottom: 2rem;
  height: auto;
  border-radius: 10px;
  padding: 50px;
  text-align: center;
} */
.projectFactsWrap .item p.number {
  font-size: 40px;
  padding: 0;
  font-weight: bold;
}
.projectFactsWrap .item span {
  width: 60px;
  background: rgba(255, 255, 255, 0.8);
  height: 2px;
  display: block;
  margin: 0 auto;
}
.projectFactsWrap .item p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 16px;
  margin: 0;
  padding: 7px 10px;
}
.projectFactsWrap .item h3 {
  font-size: 30px;
  padding: 0;
  font-weight: 400;
  color: #ffffff;
  margin: 8px 0;
}
.projectFactsWrap .item svg {
  color: #fff;
  height: 40px !important;
  width: 40px !important;
}

.projectFactsWrap .item {
  display: inline-block;
  width: 28%;
  margin: 2%;
  padding: 30px;
  text-align: center;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: #000000;
  line-height: 24px;
  transition: all 0.3s ease;
}
.projectFactsWrapOrg .item {
  display: inline-block;
  width: 20%;
  margin: 2%;
  padding: 30px;
  text-align: center;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: #000000;
  line-height: 24px;
  transition: all 0.3s ease;
}


.projectFactsWrap .item .count {
  font-size: 48px;
  line-height: 48px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #ff4444;
}

.projectFactsWrap .item .count {
  font-size: 28px;
  line-height: 28px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #ff4444;
}

@media (max-width: 1138px) {
  .projectFactsWrap .item {
    width: 45%;
  }
}

@media (max-width: 768px) {
  .projectFactsWrap .item {
    width: 100%;
  }
}
/* media queries */
